.rating {
  display: flex;
  flex-direction: column;
  margin-right: 12px;
}

.rating .count {
  font-weight: bold;
  text-align: center;
  color: #3d4953;
}

.rating .count.up {
  color: #4f9eed;
}

.rating .count.down {
  color: #ed4f4f;
}

.rating button#thumbs_up,
.rating button#thumbs_down {
  border: none;
  background: none;
  cursor: pointer;
  color: #3d4953;

  outline: none;
}

.rating button#thumbs_up > *,
.rating button#thumbs_down > * {
  color: #3d4953;
}

.rating #thumbs_up.selected > * {
  color: #4f9eed;
}

.rating #thumbs_down.selected > * {
  color: #ed4f4f;
}
